import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import QvipleLoader from "../../Loader/QvipleLoader";
import CreateButton from "../../Ui/Button/CreateButton";
import FormInputView from "../../Ui/Input/FormInputView";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useState } from "react";
import { slashDateFormat } from "../../Utils/Functions/dateUtility";
import { useStudentMcqExamStartTest } from "../../Hooks/ApiHooks/StudentManager/student-manager-api";
import dayjs from "dayjs";
import { useNavigate, useParams } from "react-router-dom";
import { Notfication } from "../../../validation/Snackbar";
const StudentStartMcq = ({ testset, onClose, studentId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const params = useParams();
  const navigate = useNavigate();
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const [studentMcqExamStartTest] = useStudentMcqExamStartTest();
  const onStartMcq = () => {
    if (testset?._id) {
      setDisabled((pre) => !pre);
      let dt = new Date();
      studentMcqExamStartTest({
        stid: testset?._id,
        bodyParameter: {
          date: dayjs(dt).format("YYYY-MM-DD"),
          time: `${dayjs(dt).format("hh:mm a")}`,
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          if (res?.data?.test_access) {
            navigate(`/q/${params.username}/student/mcq/question/sheet`, {
              state: {
                tsid: testset?._id,
                testset: testset,
                sid: studentId,
              },
            });
          } else {
            setNotificationState({
              msg: t("mcq_test_is_not_started_yet"),
              run: true,
            });
          }
        })
        .catch({});
    }
  };

  //   console.info("testset", testset);

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{testset?.testExamName ?? ""}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInputView
              label={`${t("subject_only")} :`}
              value={testset?.testSubject ?? ""}
            />
            <FormInputView
              label={`${t("marks")} :`}
              value={testset?.testTotalNumber ?? ""}
            />
            <FormInputView
              label={`${t("date")} :`}
              value={slashDateFormat(testset?.testDate)}
            />
            <FormInputView
              label={`${t("from")} :`}
              value={testset?.testStart ?? ""}
            />
            <FormInputView
              label={`${t("to")}`}
              value={testset?.testEnd ?? ""}
            />
            <FormInputView
              label={`${t("duration")} :`}
              value={`${testset?.testDuration ?? ""} Min`}
            />
            <CreateButton label="start_exam" onAction={onStartMcq} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
      <Notfication
        msg={notificationState.msg}
        run={notificationState.run}
        setRun={() => setNotificationState({ msg: "", run: false })}
        postiton="bottom"
        type="error"
      />
    </>
  );
};

export default StudentStartMcq;
