import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  Link,
  useLocation,
  useParams,
  useSearchParams,
} from "react-router-dom";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import UniversalTabWithState from "../../../../staff/Mainbody/DepartmentHead/Competetions/CompetitionTab/UniversalTabWithState";
import style from "../../../../staff/Mainbody/SubjectTeacher/Academics/Academics.module.css";
import { useStudentSubjectList } from "../../../../../hooks/member_tab/student-api";
import HorizontalTabWrapper from "../../../../../optimized/Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../../optimized/Ui/Tab/Horizontal/HorizontalTabCard";

const StudentAcademicTab = ({ carryParentState, sid, cid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const [tabList, setTabList] = useState([
    {
      label: t("all"),
      id: 0,
      url: "member/student/class?a=academic",
      groupIs: ["", null],
      carryParentState: {
        onClickValue: carryParentState?.onClickValue,
        sid: carryParentState?.sid,
        selectedTab: 0,
      },
    },
  ]);

  const { studentSubjectList, studentSubjectListRefetch } =
    useStudentSubjectList({
      data: {
        sid: sid,
        cid: cid,
      },
      skip: !sid ? !sid : !cid,
    });
  useEffect(() => {
    if (sid && cid) studentSubjectListRefetch();
  }, [sid, cid, studentSubjectListRefetch]);

  useEffect(() => {
    if (studentSubjectList?.all_subject) {
      let list = [
        {
          label: t("all"),
          id: 0,
          url: "member/student/class?a=academic",
          groupIs: ["", null],
          carryParentState: {
            onClickValue: carryParentState?.onClickValue,
            sid: carryParentState?.sid,
            selectedTab: 0,
          },
        },
      ];
      for (let sub of studentSubjectList?.all_subject) {
        list.push({
          label: sub?.subjectName,
          id: list.length,
          groupIs: [sub?.subjectName],
          url: `member/student/class?a=academic&type=${sub?.subjectName}`,
          carryParentState: {
            ...carryParentState,
            subjectName: sub?.subjectName,
            subjectId: sub?._id,
            selectedTab: list.length,
          },
        });
      }
      setTabList(() => list);
    }
  }, [studentSubjectList?.all_subject]);

  return (
    <>
      <HorizontalTabWrapper
        customWrapperStyle={{
          overflowX: "auto",
          gap: "1rem",
        }}
      >
        {tabList.map((tab) => (
          <HorizontalTabCard
            customStyle={{
              minWidth: "fit-content",
            }}
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("type"))}
            parentState={tab.carryParentState}
            url={`/q/${params.username}/${tab?.url}`}
          />
        ))}
      </HorizontalTabWrapper>

      {/* <UniversalTabWithState
        tabList={tabList}
        activeIndexTab={getQuery.state?.selectedTab}
        customStyleTab={{
          width: "100%",
        }}
        customStyleLink={{
          width: `${100 / studentSubjectList?.all_subject?.length}%`,
        }}
      /> */}
      <BorderBottom
        customStyle={{
          width: "100%",
          marginBottom: "0.7rem",
        }}
      />
      {![undefined, null, ""]?.includes(searchParams.get("type")) ? (
        <Link
          to={`/q/${params.username}/member/student/class?a=subject-academic`}
          state={{
            ...carryParentState,
            isView: "STUDENT",
          }}
        >
          <div className={style.student_view_academic_btn}>
            <h6>{t("view_academics")}</h6>
            <img src="/images/forward-right-icon.svg" alt="view academics" />
          </div>
        </Link>
      ) : null}
    </>
  );
};

export default StudentAcademicTab;
