import { useState } from "react";
import style from "../Exam.module.css";
import Modal from "../../../../../../Modal/Modal";
import ViewResultPop from "./ViewResultPop";
import { slashDateFormat } from "../../../../../../optimized/Utils/Functions/dateUtility";
import StudentStartMcq from "../../../../../../optimized/UserStudentMember/StudentMcq/StudentStartMcq";
function Testset({ testset, sid }) {
  const [open, setOpen] = useState(false);
  const [step, setStep] = useState("");
  const onCloseStep = () => {
    setStep("");
  };

  const onOpenTestset = () => {
    if (testset?.testSetComplete) {
      setOpen(true);
    } else {
      setStep(1);
    }
  };
  return (
    <>
      <div className={style.subject} onClick={onOpenTestset}>
        <div className={style.subjectLeft}>
          <img src="/images/mcq-exam-icon.svg" alt="mcq exam icon related" />
          <div className={style.text}>
            <h6>{testset?.testExamName}</h6>
            <p>{testset?.testSubject}</p>
          </div>
        </div>
        <div className={style.subjectRight}>
          <h6>{slashDateFormat(testset?.testDate)}</h6>
          <p>
            {testset?.testStart ?? ""} to {testset?.testEnd ?? ""}
          </p>
        </div>
      </div>
      {open && testset?.testSetComplete && (
        <Modal
          open={open}
          setOpen={setOpen}
          modalTitle={testset?.testExamName}
          button="no"
        >
          <ViewResultPop tsid={testset?._id} />
        </Modal>
      )}

      {step === 1 ? (
        <StudentStartMcq
          testset={testset}
          onClose={onCloseStep}
          studentId={sid}
        />
      ) : null}
    </>
  );
}

export default Testset;
