import { useState } from "react";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "./StudentAttendence.module.css";
import { ClassTimetableCard } from "./TimeTableCard/TimeTableCard";
import { useStudentDateWiseTimetable } from "../../../../../optimized/Hooks/ApiHooks/StudentManager/student-manager-api";
import Loading from "../../../../../optimized/Loader/Loading";
import EmptyMessage from "../../../../../optimized/Utils/EmptyMessage/EmptyMessage";

const StudentTimeTable = ({ sid }) => {
  const { t } = useTranslation();
  const week = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];
  const [timeData, setTimeData] = useState({
    status: week[new Date()?.getDay()],
    date: new Date().toISOString().split("T")[0],
  });

  const { studentDateWiseTimetable, studentDateWiseTimetableLoading } =
    useStudentDateWiseTimetable({
      data: {
        sid: sid,
        // status: "Monday",
        status: timeData?.status,
        date: timeData?.date,
      },
      skip: !sid,
    });

  return (
    <div
      className={style.staffAttendence}
      style={{
        display: "block",
      }}
    >
      <p>{t("class_time_table")}</p>
      <BorderBottom
        customStyle={{
          width: "100%",
        }}
      />
      {studentDateWiseTimetable?.map((schedule, index) => (
        <div key={index} className={style.itemref}>
          <ClassTimetableCard schedule={schedule} />
        </div>
      ))}

      {studentDateWiseTimetable?.length === 0 && (
        <EmptyMessage
          customStyleContainer={{
            marginTop: "1.5rem",
          }}
          title="No subject schedule for this day."
        />
      )}

      {studentDateWiseTimetableLoading && <Loading />}
    </div>
  );
};

export default StudentTimeTable;
